import React, {Component} from 'react';
import {Link} from "react-router-dom";

class GreetingComponent extends Component {
    constructor(props) {
        super(props);

        this.gotToMainPage = this.gotToMainPage.bind(this)
    }
    gotToMainPage() {
        console.log("clicked")
    }
    render() {
        return (
            <div>
                <div class="jumbotron">
                    <h1 class="display-4"> </h1>
                    <p class="lead">        .</p>
                    <hr class="my-4"></hr>
                        <p>   ,    !</p>
                        <p class="lead">
                            <a className="btn btn-primary btn-lg" onClick={this.gotToMainPage} href="/main"></a>
                            <a class="btn btn-secondary btn-lg" href="#" role="button"></a>
                        </p>
                </div>

                <div class="container text-center">
                    <h2 class="mb-4"> </h2>
                    <div class="row">
                        <div class="col-md-4 mb-4">
                            <div class="animated fadeInDown delay-1s">
                                <i class="fas fa-link fa-3x mb-2"></i>
                                <h4> </h4>
                                <p>       .</p>
                            </div>
                        </div>
                        <div class="col-md-4 mb-4">
                            <div class="animated fadeInDown delay-2s">
                                <i class="fas fa-chart-line fa-3x mb-2"></i>
                                <h4></h4>
                                <p>      .</p>
                            </div>
                        </div>
                        <div class="col-md-4 mb-4">
                            <div class="animated fadeInDown delay-3s">
                                <i class="fas fa-lock fa-3x mb-2"></i>
                                <h4></h4>
                                <p>      .</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default GreetingComponent;